@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "PT Mono";
  font-display: swap;
  src: url("../fonts/ptmono-regular.woff2") format("woff2"), url("../fonts/ptmono-regular.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Roboto Condensed";
  font-display: swap;
  src: url("../fonts/robotocondensed-regular.woff2") format("woff2"), url("../fonts/robotocondensed-regular.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 700;
  font-family: "Roboto Condensed";
  font-display: swap;
  src: url("../fonts/robotocondensed-bold.woff2") format("woff2"), url("../fonts/robotocondensed-bold.woff") format("woff");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "Placeholder", "Arial", sans-serif;
  color: #000000;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: #ffffff;
}

a {
  color: #000000;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

html {
  scroll-behavior: smooth;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.rouble {
  font-family: "Rouble", "Arial", sans-serif;
  /* stylelint-disable */
  text-transform: lowercase !important;
  font-weight: 400 !important;
  /* stylelint-enable */
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.container {
  width: 100%;
  max-width: 1260px;
  margin: 0 auto;
  padding: 0 20px;
}
@media (max-width: 1023px) {
  .container {
    padding: 0 50px;
  }
}
@media (max-width: 767px) {
  .container {
    padding: 0 25px;
  }
}

.header {
  position: fixed;
  width: 100%;
  min-height: 120px;
  background: linear-gradient(180deg, #041827 84.9%, rgba(4, 24, 39, 0) 100%);
  z-index: 2;
}
@media (max-width: 1023px) {
  .header {
    position: static;
    min-height: 100px;
  }
}
@media (max-width: 767px) {
  .header {
    min-height: 70px;
  }
}

.header__container {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  padding-top: 30px;
  padding-right: 80px;
  padding-bottom: 35px;
}
@media (max-width: 1023px) {
  .header__container {
    padding-top: 20px;
    padding-right: 70px;
    padding-bottom: 25px;
  }
}
@media (max-width: 767px) {
  .header__container {
    padding-top: 10px;
    padding-bottom: 20px;
    padding-right: 35px;
  }
}

.header__list {
  display: flex;
  align-items: center;
  gap: 30px;
  margin: 0;
  padding: 0;
  padding-left: 15px;
  flex-wrap: wrap;
  list-style-type: none;
}
@media (max-width: 1023px) {
  .header__list {
    gap: 20px;
    padding-left: 0;
  }
}

.header__link {
  position: relative;
  display: block;
  min-width: 225px;
  padding: 15px;
  padding-left: 35px;
  font-family: "PT Mono", "Courier New", monospace;
  font-size: 16px;
  line-height: 18px;
  color: #f2f5f6;
  transition: color 0.3s ease;
}
@media (hover: hover) {
  .header__link:hover:not(.focus-visible) {
    color: #00b2ff;
  }
}
.header__link.focus-visible:focus {
  color: #00b2ff;
}
.header__link::before {
  position: absolute;
  content: "";
  top: 50%;
  left: 0;
  transform: translateY(-50%);
  width: 20px;
  height: 20px;
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
}
@media (max-width: 1023px) {
  .header__link::before {
    left: 50%;
    transform: translate(-50%, -50%);
  }
}
@media (max-width: 1023px) {
  .header__link {
    min-width: 40px;
    min-height: 40px;
    padding: 10px;
  }
}
.header__link--mail::before {
  background-image: url("../img/svg/email.svg");
}
.header__link--phone::before {
  background-image: url("../img/svg/phone-call.svg");
}

@media (max-width: 1023px) {
  .header__text {
    display: none;
  }
}

.header__button {
  display: block;
  min-width: 160px;
  margin-top: 5px;
  padding: 10px 15px;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-weight: 700;
  font-size: 14px;
  line-height: 16px;
  text-align: center;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  color: #f2f5f6;
  border: 1px solid #f2f5f6;
  border-radius: 3px;
  transition: color, border-color 0.3s ease;
  user-select: none;
}
@media (hover: hover) {
  .header__button:hover:not(.focus-visible) {
    color: #00b2ff;
    border-color: #00b2ff;
  }
}
@media (max-width: 1023px) {
  .header__button {
    display: none;
  }
}

.logo {
  flex-shrink: 0;
}
@media (max-width: 767px) {
  .logo {
    margin-top: 5px;
  }
}
@media (max-width: 767px) {
  .logo--footer {
    margin-top: 0;
    padding-right: 5px;
  }
}

.logo__icon {
  display: block;
}
@media (max-width: 767px) {
  .logo__icon {
    display: none;
  }
}
.logo__icon--mobile {
  display: none;
}
@media (max-width: 767px) {
  .logo__icon--mobile {
    display: block;
  }
}
@media (max-width: 767px) {
  .logo__icon--footer {
    display: block;
  }
}

.intro {
  position: relative;
}

.intro__background {
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
  background-color: #041827;
}
@media (max-width: 1023px) {
  .intro__background {
    object-position: -123px 0;
  }
}
@media (max-width: 767px) {
  .intro__background {
    object-position: -229px 0;
  }
}

.intro__container {
  display: flex;
  flex-direction: column;
  margin-bottom: 120px;
  padding-top: 305px;
  padding-left: 145px;
  padding-bottom: 280px;
}
@media (max-width: 1023px) {
  .intro__container {
    margin-top: -100px;
    margin-bottom: 80px;
    padding-top: 180px;
    padding-bottom: 205px;
  }
}
@media (max-width: 767px) {
  .intro__container {
    margin-top: -70px;
    margin-bottom: 50px;
    padding-top: 150px;
    padding-bottom: 190px;
  }
}

.intro__container h2 {
  margin-top: 0;
  margin-bottom: 32px;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-weight: 700;
  font-size: 80px;
  line-height: 94px;
  letter-spacing: 0.07em;
  text-transform: uppercase;
  color: #f2f5f6;
}
@media (max-width: 1023px) {
  .intro__container h2 {
    margin-bottom: 0;
    font-size: 64px;
    line-height: 75px;
  }
}
@media (max-width: 767px) {
  .intro__container h2 {
    margin-bottom: 8px;
    font-size: 36px;
    line-height: 42px;
  }
}

.intro__container p {
  max-width: 685px;
  margin-top: 0;
  margin-bottom: 45px;
  font-family: "PT Mono", "Courier New", monospace;
  font-weight: 400;
  font-size: 22px;
  line-height: 155%;
  color: #f2f5f6;
  text-transform: lowercase;
}
@media (max-width: 1023px) {
  .intro__container p {
    margin-bottom: 57px;
  }
}
@media (max-width: 767px) {
  .intro__container p {
    margin-bottom: 45px;
    font-size: 14px;
  }
}

.intro__link {
  display: block;
  max-width: 410px;
  padding: 15px 25px 19px;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-weight: 700;
  font-size: 18px;
  line-height: 21px;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  color: #f2f5f6;
  background-color: #00b2ff;
  border-radius: 3px;
  user-select: none;
  transition: background-color 0.3s ease;
}
@media (max-width: 1023px) {
  .intro__link {
    margin-left: 45px;
  }
}
@media (max-width: 767px) {
  .intro__link {
    max-width: 270px;
    margin-left: 0;
    padding: 12px 30px;
    font-size: 14px;
    line-height: 16px;
    text-align: center;
  }
}
@media (hover: hover) {
  .intro__link:hover:not(.focus-visible) {
    background-color: #005b82;
  }
}

@media (max-width: 767px) {
  .intro__text-link--desktop {
    display: none;
  }
}
.intro__text-link--mobile {
  display: none;
}
@media (max-width: 767px) {
  .intro__text-link--mobile {
    display: block;
  }
}

.advantages__container {
  margin-bottom: 120px;
}
@media (max-width: 1023px) {
  .advantages__container {
    margin-bottom: 80px;
  }
}
@media (max-width: 767px) {
  .advantages__container {
    margin-bottom: 50px;
  }
}

.advantages__list {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 30px;
  margin: 0;
  padding: 0;
  list-style-type: none;
}
@media (max-width: 767px) {
  .advantages__list {
    grid-template-columns: 1fr;
  }
}

.advantages__item {
  display: flex;
  flex-direction: column;
  min-height: 300px;
  padding: 40px 45px 35px 40px;
  border: 1px solid #ebebeb;
  border-left: 5px solid #00b2ff;
}
@media (hover: hover) {
  .advantages__item:hover:not(.focus-visible) {
    color: #f2f5f6;
    background-color: #00b2ff;
    border-color: #00b2ff;
  }
  .advantages__item:hover:not(.focus-visible):hover .advantages__icon {
    fill: #f2f5f6;
  }
  .advantages__item:hover:not(.focus-visible):hover h3 {
    color: #f2f5f6;
  }
  .advantages__item:hover:not(.focus-visible):hover p {
    color: #f2f5f6;
  }
}
@media (max-width: 1023px) {
  .advantages__item {
    min-height: 360px;
    padding-right: 30px;
    padding-bottom: 35px;
  }
}
@media (max-width: 767px) {
  .advantages__item {
    min-height: 340px;
    padding-top: 30px;
    padding-left: 25px;
  }
}
@media (max-width: 767px) {
  .advantages__item--long-title {
    min-height: 325px;
  }
}

.advantages__icon {
  display: block;
  margin-bottom: 20px;
  fill: #00b2ff;
}
@media (max-width: 1023px) {
  .advantages__icon {
    margin-bottom: 14px;
  }
}
@media (max-width: 767px) {
  .advantages__icon {
    width: 30px;
    height: 30px;
  }
}

.advantages__item h3 {
  margin-top: 0;
  margin-bottom: 15px;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-weight: 400;
  font-size: 24px;
  line-height: 140%;
  color: #00b2ff;
}
@media (max-width: 1023px) {
  .advantages__item h3 {
    margin-bottom: 9px;
  }
}
@media (max-width: 767px) {
  .advantages__item h3 {
    font-size: 20px;
  }
}

.advantages__item p {
  margin: 0;
  font-family: "PT Mono", "Courier New", monospace;
  font-size: 16px;
  line-height: 135%;
  color: #041827;
}
@media (max-width: 767px) {
  .advantages__item p {
    font-size: 14px;
  }
}

@media (max-width: 1023px) {
  .advantages__item--long-title .advantages__icon {
    margin-bottom: 10px;
  }
}
@media (max-width: 767px) {
  .advantages__item--long-title .advantages__icon {
    margin-bottom: 17px;
  }
}

@media (max-width: 1023px) {
  .advantages__item--long-title h3 {
    margin-bottom: 5px;
  }
}
@media (max-width: 767px) {
  .advantages__item--long-title h3 {
    margin-bottom: 14px;
  }
}

.about__container {
  margin-bottom: 100px;
  display: grid;
  grid-template-columns: auto 1fr;
  gap: 100px;
}
@media (max-width: 1023px) {
  .about__container {
    margin-bottom: 80px;
    gap: 29px;
    margin-left: -4px;
  }
}
@media (max-width: 767px) {
  .about__container {
    grid-template-columns: 1fr;
    margin-left: 0;
    gap: 0;
  }
}

.about__image {
  display: block;
}
@media (max-width: 767px) {
  .about__image {
    display: none;
  }
}

.about__wrapper {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
}

.about__container h2 {
  margin-top: 0;
  margin-bottom: 11px;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-weight: 400;
  font-size: 36px;
  line-height: 51px;
  color: #041827;
}
@media (max-width: 1023px) {
  .about__container h2 {
    margin-top: -7px;
  }
}
@media (max-width: 767px) {
  .about__container h2 {
    margin-top: 0;
    font-size: 28px;
    line-height: 39px;
  }
}

.about__container p {
  margin-top: 0;
  margin-bottom: 22px;
  justify-self: flex-start;
  font-family: "PT Mono", "Courier New", monospace;
  font-size: 16px;
  line-height: 21.5px;
